export default {
  props:{
    closable:{
      default:false,
      type:Boolean
    },
    type:{
      default:"info",
      type:String
    },
    icon:{
      default:""
    },
    noIcon:{
      default:false,
      type:Boolean
    }
  },
  data(){
    return{
      show:true
    }
  },
  computed:{
    iconComputed(){
        if(this.type=='info'){
          return 'info'
        }
        else if(this.type=='success'){
          return 'check_circle'
        }
        else if(this.type=='error'){
          return 'error'
        }
        else if(this.type=='warning'){
          return 'warning'
        }
    },
  }
}
