export default {
props:{
 size:{
   default:20,
   required:false
 },
 light:{
   default:false,
   required:false,
   type:Boolean
 },
  dark:{
   default:false,
   required:false,
   type:Boolean
 },
  disabled:{
   default:false,
   required:false,
   type:Boolean
 },
   cursor:{
   default:false,
   required:false,
   type:Boolean
 },
  color:{
   default:"",
   required:false,
   type:String
 }
},
computed:{
  returnClass(){
    let cl = []
    if(this.light)
      cl.push("md-light")
    if(this.dark)
      cl.push("md-dark")
    if(this.disabled)
      cl.push("md-inactive")
    if(this.cursor)
      cl.push("cursor")
    return cl
  },
 returnStyle(){
   let st=[]
   st.push(`font-size:${this.size}px;`)
   if(this.color!='')
     st.push(`color:${this.color};`)
   if(this.cursor)
     st.push(`cursor:pointer;`)

   return st.join(' ')
 }
},
methods:{
  handleClick(e){
    if(this.cursor){
      this.$emit('click',e)
    }
  }
}
}
