export default {
props:{
  src:{
    default:"https://www.w3schools.com/w3images/avatar2.png",
    required:false,
  },
  alt:{
    default:"avatar",
    required:false
  },
  size:{
    default:50,
    required:false
  }
},
computed:{
  returnStyle(){
    let st = []
    st.push(`width:${this.size}px!important;height:${this.size}px!important;`)
    return st.join(' ')
  }
}
}
