export default {
  mounted(){
    let vm = this
    window.addEventListener('online', () => {
      vm.$swal.fire(
        vm.$t('internet.connected'),
        vm.$t('internet.contiuneSurf'),
        'success'
      )
    });
    window.addEventListener('offline', () => {
      vm.$swal.fire(
        vm.$t('internet.lost'),
        vm.$t('internet.sureInternet'),
        'question'
      )
    });
  }
}
